import './index.css'

import React, { Suspense } from 'react'
import { Navigate, Route, Routes } from 'react-router-dom'

import ConsoleCommonProvider from '../../services/ConsoleCommonProvider'
import AuthorizerApiDocs from '../ApiDocs/authorizer'
import Frame from './Frame'

const Evaluator = React.lazy(() => import('./views/Evaluator'))
const Modules = React.lazy(() => import('./views/Modules'))

const PolicyInstanceDetails = () => {
  return (
    <ConsoleCommonProvider>
      <Suspense fallback={<Frame></Frame>}>
        <Routes>
          <Route element={<Evaluator />} path="/evaluator" />
          <Route element={<Modules />} path="/modules" />
          <Route
            element={
              <>
                <Frame>
                  <AuthorizerApiDocs />
                </Frame>
              </>
            }
            path="/docs"
          />
          <Route element={<Navigate to="modules" />} path="*" />
        </Routes>
      </Suspense>
    </ConsoleCommonProvider>
  )
}

export default PolicyInstanceDetails
